<template>
<div>
  <div class="user-circle" @click="menu = true" :style="objStyles">
    <input type="file" @change="onFileChange" style="display: hidden" ref="input">
  </div>

  <div>{{ user.name }}</div>

  <v-bottom-sheet v-model="menu">
      <v-list dense>
        <v-list-item link @click="selectImage">
          <v-list-item-icon><v-icon>mdi-account-circle-outline</v-icon></v-list-item-icon>
          <v-list-item-content>Đổi avatar</v-list-item-content>
        </v-list-item>
        <v-list-item link>
          <v-list-item-icon><v-icon>mdi-card-text-outline</v-icon></v-list-item-icon>
          <v-list-item-content>Đổi tên</v-list-item-content>
        </v-list-item>
        <v-list-item link>
          <v-list-item-icon><v-icon>mdi-eyedropper-variant</v-icon></v-list-item-icon>
          <v-list-item-content>Đổi màu viền</v-list-item-content>
        </v-list-item>
      </v-list>
    </v-bottom-sheet>
</div>
</template>

<script>
export default {
  props: {
    user: {
      type: Object,
      required: true,
    },
    name: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      menu: false,
      avatar: '',
      ff: null
    }
  },
  computed: {
    objStyles() {
      let styles = {}
      styles.backgroundImage = 'url(' + this.avatar + ')'
      return styles
    }
  },
  mounted() {
    this.avatar = URL.createObjectURL(this.user.avatar)
  },
  methods: {
    selectImage() {
      this.$refs.input.click()
    },
    onFileChange() {
      if (this.$refs.input.files.length) {
        this.$store.commit('SET_AVATAR', {
          name: this.name,
          file: this.$refs.input.files[0]
        })
      }
    }
  },
  watch: {
    'user.avatar'(blob) {
      this.avatar = URL.createObjectURL(blob)
    }
  }
}
</script>

<style lang="scss">
.user-circle {
  display: inline-block;
  width: 140px;
  height: 140px;
  border: 3px solid #ff30a2;
  margin: 10px;
  border-radius: 50%;
  background-size: cover;

  input[type="file"] {
    display: none;
  }
}
</style>